form {
    width: 90%;
    max-width: 300px;
    background-color: rgb(250, 240, 228);
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-weight: 400;
    font-size: 18;
  }
  
  textarea:focus {
    outline-color: rgb(255, 133, 81);
  }
  
  .TodoFormMobile-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoFormMobile-button {
    cursor: pointer;
    display: inline-block;
    font-weight: 500;
    font-size: 16px;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
  }
  
  .TodoFormMobile-button--add {
    background: rgb(255, 133, 81);
  }
  
  .TodoFormMobile-button--cancel {
    background: transparent;
  }